<template>
 <div class="container mt-4">
      
      <b-row>
      <b-col md="7" class="my-1 mb-4">
        <b-form-group label-cols-sm="3" label="–ü–æ–∏—Å–∫" class="mb-0">
          <b-input-group>
            <b-form-input v-model="searchName" placeholder="–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç –¥–ª—è –ø–æ–∏—Å–∫–∞"/>
            <b-input-group-append>
              <b-button :disabled="!searchName" @click="searchName = ''">–û—á–∏—Å—Ç–∏—Ç—å</b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col> 
      <b-col>
         <b-button variant="outline-success" size="sm" v-show="editIndex !== 0 && !editIndex" @click="add">–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—É—é –∑–∞–ø–∏—Å—å</b-button>
      </b-col>    
    </b-row>     

    <b-row>       
    <table class="table table-bordered table-sm">
      <thead class="thead-light text-center">
        <tr>
          <th v-for="(item, index) in columns" :key="index" v-bind:width="item.width"> {{ item.name }} </th>
        </tr>
      </thead>
      <tbody>
         
        <!-- <tr v-for="(item, index) in items" :key="index"> -->
        <tr v-for="(item, index) in items" :key="index" v-show="filterName(item.title, item.isNew)">
          <!-- <td>{{ item }}</td> -->
          <td class="align-middle">
            <span v-if="editIndex !== index"> {{ item.streetid }} </span>
            <span v-if="editIndex === index">
              <b-form-input class="bg-light" v-model="item.streetid"></b-form-input>
            </span>           
          </td>
          <td>
            <span v-if="editIndex !== index"> {{ item.title }} </span>
            <span v-if="editIndex === index">
              <b-form-input class="bg-light" v-model="item.title"></b-form-input>
            </span>    
          </td>
          <td class="align-middle">
            <span v-if="editIndex !== index">
              <b-button variant="outline-secondary" size="sm" @click="edit(item, index)">–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å</b-button>
              <b-button variant="outline-danger" size="sm" @click="remove(item, index)">–£–¥–∞–ª–∏—Ç—å</b-button>
            </span>
            <span v-else>
              <b-button variant="outline-dark" size="sm" @click="cancel(item)">–û—Ç–º–µ–Ω–∞</b-button>
              <b-button variant="outline-success" size="sm" @click="save(item)">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</b-button>
            </span>
          </td>
        </tr>
      </tbody>
    </table>
    </b-row>
        <!-- <div class="col-3 offset-9 text-right my-3"> -->
      <b-button variant="outline-success" size="sm" v-show="editIndex !== 0 && !editIndex" @click="add">–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—É—é –∑–∞–ø–∏—Å—å</b-button>
    <!-- </div> -->

  </div>
</template>

<script>

import axios from 'axios'

export default {
//   name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      editIndex: null,
      isAdd: false,
      searchName: "",
      items: [
        {
		"streetid" : 2866,
		"title" : "—É–ª. –ò–æ—Å–∏—Ñ–∞ –ö–∞—Ä–æ–ª–∏–Ω—Å–∫–æ–≥–æ, 8"
	},
	{
		"streetid" : 2867,
		"title" : "—É–ª. –ò–æ—Å–∏—Ñ–∞ –ö–∞—Ä–æ–ª–∏–Ω—Å–∫–æ–≥–æ, 9\/1"
	},
	{
		"streetid" : 2868,
		"title" : "—É–ª. –ò–æ—Å–∏—Ñ–∞ –ö–∞—Ä–æ–ª–∏–Ω—Å–∫–æ–≥–æ, 9"
	},
	{
		"streetid" : 2869,
		"title" : "—É–ª. –ò–æ—Å–∏—Ñ–∞ –ö–∞—Ä–æ–ª–∏–Ω—Å–∫–æ–≥–æ, 10"
	},
	{
		"streetid" : 2870,
		"title" : "—É–ª. –ò–æ—Å–∏—Ñ–∞ –ö–∞—Ä–æ–ª–∏–Ω—Å–∫–æ–≥–æ, 10 —Å–æ–æ—Ä. 1"
	},
	{
		"streetid" : 2871,
		"title" : "—É–ª. –ò–æ—Å–∏—Ñ–∞ –ö–∞—Ä–æ–ª–∏–Ω—Å–∫–æ–≥–æ, 11 —Å–æ–æ—Ä. 1"
	}],

    }
  },
  props: {
    columns: null
    // items: null
  },
  methods: {  

     add() {      
      this.items.push({ streetid: '', title: '', isNew: true});
      this.editIndex = this.items.length - 1;
      this.isAdd = true;
      this.searchName = "–ò–¥–µ—Ç –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–π –∑–∞–ø–∏—Å–∏...";
    },

    —ÅlearSearch() {
      this.searchName = '';

    },
    edit(item, index) {
      this.editIndex = index;
    },

    cancel(item) {
      this.editIndex = null ;
      if (this.isAdd) {
      this.items.splice(this.items.indexOf(item), 1);
      this.isAdd = false;
      this.searchName = '';
      this.isNew = false;
      }
    },

    remove(item, index) {
      this.items.splice(index, 1);
    },

    save(item) {
      this.editIndex = null;
      if (item.isNew) {
        alert('–ó–∞–ø–∏—Å—å –¥–æ–±–∞–≤–ª–µ–Ω–∞!')
      }
      else{
        alert('–ó–∞–ø–∏—Å—å —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∞!')
      }
      item.isNew = false;
      this.searchName = '';
      

    },
    filterName(itemname, isnew) {

        if (itemname.toLowerCase().includes(this.searchName.toLowerCase()) || isnew) {      
        return true;
      }       
       return false;
    },
  },
}
</script>

<style scoped>
span {
    font-size: 15px;
  }
.table .thead-light th {
  color: white;
  background-color: #007bff;
  border-color: #dee2e6;
}
</style>



